import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import Layout from "../components/layout/layout"
import Metadata from "../components/metadata/metadata"
import Container from "react-bootstrap/Container"
import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"

import govStyles from "../styles/governance.module.css"


const Governance = () => {
    const data = useStaticQuery(
    graphql`
        query{
            file(relativePath: { eq: "images/stripes/White_Mountain_Range_Top.jpg" }) {
                childImageSharp {
                    fluid(maxWidth: 960, maxHeight: 150) {
                        ...GatsbyImageSharpFluid
                    }
                }
            }
        }
    `
    )

    return (
        <Layout>
            <Metadata 
                title="Governance"
                description="At Hope Labs we follow strict governance policies to ensure integrity and transparency."
            />
            <Container className={govStyles.governanceMain}>
                <Row>
                    <Img fluid={data.file.childImageSharp.fluid} alt="White Mountain range"/>
                </Row>
                <Row>
                    <Col>
                        <h1>Governance Principles</h1>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <p>Explanations</p>
                    </Col>
                </Row>
            </Container>
        </Layout>
    )
    }

    export default Governance 
